@media screen and (max-width: 425px) {
  .swiper::before {
    z-index: 1000;
    position: absolute;
    top: 75px;
    right: 0;
    content: '';
    display: inline-block;
    width: 24px;
    height: calc(100% - 110px);
    background: linear-gradient(
      270deg,
      #f8f8f8 16.84%,
      rgba(248, 248, 248, 0.0001) 100%
    );
  }

  .swiper-pagination {
    display: block;
    position: relative !important;
    margin-top: 9px;
    margin-bottom: 8px;
  }

  .swiper-slide {
    width: 240px;
    height: 72px;
  }

  .swiper {
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }
}

@media screen and (min-width: 425px) {
  .swiper-pagination {
    display: none;
  }
}
